import React, { Component } from "react";

class SideNavMenuItem extends Component {
    render() {
        return (<>
            {!this.props.readOnly && <button onClick={() => this.props.handleClick(this.props.value)} className={`list-group-item p-2 ${this.props.value === this.props.selectedSideNavValue ? ' active' : ''}`}><i className={`fa ${this.props.icon} text-muted`}></i> {this.props.value}</button>}</>
        );
    }
}
export default SideNavMenuItem;