function getUserTeamsFromUserItem() {
  let userTeams = [];

  let currentUser = getCurrentUser();
  if (currentUser) {
    userTeams.push({ id: currentUser.defaultUserTeamDetails.id, name: currentUser.defaultUserTeamDetails.name, isChecked: true, default: true });

    currentUser.additionalUserTeamDetails && currentUser.additionalUserTeamDetails.forEach(userTeam => {
      if (userTeam.id !== currentUser.defaultUserTeamDetails.id) {
        userTeams.push({ id: userTeam.id, name: userTeam.name, isChecked: false, default: false });
      }
    });
  }
  return userTeams;
}

function getCurrentUserTeamIds() {
  return getUserTeamsFromUserItem().map(userTeam => userTeam.id);
}

