function filterEmployees(items, filterOptions) {
  //console.log(filterOptions)
  if (!items) {
    return items;
  }

  if (filterOptions && filterOptions.SHOW) {

    if (filterOptions.SHOW.selected === 'ACTIVE') {
      items = items.filter(employee => employee.active)
    }
    if (filterOptions.SHOW.selected === 'INACTIVE') {
      items = items.filter(employee => !employee.active)
    }
  }

  if (filterOptions && filterOptions.filterText && filterOptions.filterText && filterOptions.filterText.length > 2) {
    items = items.filter(employee =>
      commonUtils.stringContainsString(employee.name, filterOptions.filterText)
      || commonUtils.stringContainsString(employee.email, filterOptions.filterText)
      || (employee.empCode && commonUtils.stringContainsString(employee.empCode, filterOptions.filterText))
      || (employee.designation && commonUtils.stringContainsString(employee.designation, filterOptions.filterText))
      || (employee.primarySkill && commonUtils.stringContainsString(employee.primarySkill, filterOptions.filterText))
    )
  }
  if (filterOptions && filterOptions.SORT) {
    items = commonUtils.sortArrayOfObjects(items, filterOptions.SORT.selected === 'A-Z');
  }
  return commonUtils.sortArrayOfObjects(items, true);
}

