import React, { Component } from "react";
import { connect } from "react-redux";
import { accessUtils } from "_helpers";
import TaskDetailsGeneral from "../TaskDetailsGeneral";

class ViewTaskDetailsSidebar extends Component {
  constructor() {
    super();
    this.state = {
      permission: accessUtils.getTaskAccessControl()
    }
  }
  render() {
    return (
      <div id="createtasksidebar" className={`right_sidebar view_task_right_sidebar  ${this.props.show ? "open" : ""}`}>
        <div className="justify-content-between flex-column d-flex h-100vh">
          <div className="card-header py-0 border-bottom">
            <h3 className="card-title">View Deliverable Details</h3>
            <div className="card-options">
              <button onClick={this.props.onCloseViewTask} className="link p-2 create-project-close-link float-right"><i className="fa fa-close" /></button>
            </div>
          </div>
          {this.props.show &&
           <div className="card-body pl-3 pr-3 overflow-auto">
            <TaskDetailsGeneral
              item={this.props.taskInfo} 
              readOnly={true}
              permission={this.state.permission}
            />
            </div>
          }
        </div>
      </div>
    );
  }
}
const mapStateToProps = state => ({
  taskInfo: state.tasks.selectedTaskInfo,
  isLoading: state.tasks.loading
});

const mapDispatchToProps = dispatch => ({

});

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(ViewTaskDetailsSidebar);

